import {
	mapActions,
	mapState,
	mapMutations
} from 'vuex'

import {
	uniPopup
} from '@dcloudio/uni-ui'
export default {
	components:{
		uniPopup
	},
	data() {
		return {
			title: 'Hello',
			news: [],
			SessionKey: '',
			OpenId: '',
			code: '',
			nickName: null,
			avatarUrl: null,
			// isCanUse: uni.getStorageSync('isCanUse') || false //默认为false
		}
	},
	computed:{
		...mapState(['token'])
	},
	onLoad() {
		this.login()
	},
	// onShow(){
	// 	this.getAdress()
	// },
	methods: {
		...mapActions(['userLogin']),
		...mapMutations(['SET_VALUE']),
		// 跳转到商品列表
		toList(){
			uni.switchTab({
				url:'../goodsList/goodsList',
				fail:(err)=>{
					console.log(err)
				}
			})
		},
		// 获取当前位置
		getAdress(){
			uni.getLocation({
			    type: 'wgs84',
			    success: function (res) {
			        console.log('当前位置的经度：' + res.longitude);
			        console.log('当前位置的纬度：' + res.latitude);
			    }
			})
		},
		wxGetUserInfo(infoRes) {
			this.$refs.popup.close()
			console.log(infoRes)
			let {
				detail
			} = infoRes
			this.SET_VALUE({key:'userInfo',value:detail.userInfo})
			this.nickName = detail.userInfo.nickName; //昵称
			this.avatarUrl = detail.userInfo.avatarUrl; //头像
			try {
				// uni.setStorageSync('isCanUse', true); //记录是否第一次授权  false:表示不是第一次授权
				// _this.updateUserInfo();
				this.loginRequest()
				// this.isCanUse = true
			} catch (e) {
				console.log(e)
			}
		},

		//登录
		login() {
			let _this = this;
			uni.showLoading({
				title: '登录中...'
			});
			//





			// 1.wx获取登录用户code
			uni.login({
				provider: 'weixin',
				success(loginRes) {
					let code = loginRes.code;
					_this.code = code
					console.log('code', code)
					uni.getUserInfo({
						provider: 'weixin',
						success(infoRes) {
							// console.log('infoRes',infoRes.userInfo)
							//获取用户信息后向调用信息更新方法
							_this.SET_VALUE({key:'userInfo',value:infoRes.userInfo})
							_this.nickName = infoRes.userInfo.nickName; //昵称
							_this.avatarUrl = infoRes.userInfo.avatarUrl; //头像
							// _this.updateUserInfo(); //调用更新信息方法
							//2.将用户登录code传递到后台置换用户SessionKey、OpenId等信息
							_this.loginRequest()
						},
						fail(err) {
							console.log('uni.getUserInfo失败',err)
							_this.$refs.popup.open()
							// throw new Error('登录失败，需要重新登录')
						}
					});
					uni.hideLoading();

				},
				fail(err) {
					console.log('uni.login失败',err)
					_this.$refs.popup.open()
				}
			});


		},
		// 登录请求
		async loginRequest() {
			await this.userLogin({
				params: {
					code: this.code,
					nickName: this.nickName,
					avatarUrl: this.avatarUrl
				},
				stateName: 'token'
			})
			console.log('token',this.token)
		}
	}
}
